.page-header {
  padding: 10px 0;
  min-height: 85px;
}

.page-header__logo {
  margin-right: 36px;

  img {
    vertical-align: baseline;
  }
}

.page-header__navbar-toggler {
  margin-right: -15px;
}

.page-header__contacts {
  display: inline-block;
  margin-top: 8px;

  font-size: $largFontSize;
  font-weight: $mediumFontWeight;
  line-height: $largLineHeght;
}

@media (min-width: $media-breakpoint-md) {
  .page-header__logo {
    img {
      vertical-align: middle;
    }
  }
}

@media (min-width: $media-breakpoint-lg) {
  .page-header {
    .container {
      padding: 0;
    }
  }
}

@media (min-width: $media-breakpoint-xl) {
  .page-header__logo {
    img {
      width: 134px;
      height: 33px;
    }
  }
}
