body {
  padding: 0;
  min-width: 320px;

  font-family: "Roboto", sans-serif;
  color: $color-base;
}

a {
  color: $color-base;

  &:hover {
    color: $hover-color;
    text-decoration: none;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.collapse {
  &.show {
    & + button {
      transform: rotate(180deg);
    }
  }
}
