@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap");
html {
  line-height: 1.15; }

body {
  margin: 0; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace;
  font-size: 1em; }

a {
  background-color: transparent; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */ }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  outline-offset: -2px; }

::-webkit-file-upload-button {
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

body {
  padding: 0;
  min-width: 320px;
  font-family: "Roboto", sans-serif;
  color: #005bc1; }

a {
  color: #005bc1; }
  a:hover {
    color: #312783;
    text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.collapse.show + button {
  transform: rotate(180deg); }

.page-header {
  padding: 10px 0;
  min-height: 85px; }

.page-header__logo {
  margin-right: 36px; }
  .page-header__logo img {
    vertical-align: baseline; }

.page-header__navbar-toggler {
  margin-right: -15px; }

.page-header__contacts {
  display: inline-block;
  margin-top: 8px;
  font-size: 18px;
  font-weight: 500;
  line-height: 21px; }

@media (min-width: 768px) {
  .page-header__logo img {
    vertical-align: middle; } }

@media (min-width: 992px) {
  .page-header .container {
    padding: 0; } }

@media (min-width: 1200px) {
  .page-header__logo img {
    width: 134px;
    height: 33px; } }

.main-nav__logo {
  margin-right: 32px; }

.nav-link {
  padding: 8px 7px 4px;
  font-size: 13px;
  line-height: 15px;
  color: #005bc1; }
  .nav-link:hover {
    color: #312783; }

@media (min-width: 768px) {
  .nav-item {
    position: relative; }
    .nav-item.active .nav-link::before, .nav-item:hover .nav-link::before {
      content: "";
      position: absolute;
      top: -25px;
      left: 0;
      width: 100%;
      height: 10px;
      background: radial-gradient(50% 50% at 50% 50%, #0092f2 27.6%, #006be9 100%); } }

.about {
  padding-top: 1px;
  padding-bottom: 1px;
  font-size: 18px;
  line-height: 21px;
  color: #ffffff;
  background: radial-gradient(50% 50% at 50% 50%, #0092f2 27.6%, #006be9 100%); }
  .about img {
    position: relative;
    display: block; }
  .about p {
    margin-bottom: 21px; }

.about__wrapper {
  margin: 29px 17px; }

.about__contacts {
  margin-top: 40px;
  max-width: 313px; }

.about__text {
  margin: 0; }

.about__show-all-text {
  display: block;
  margin: 0 auto;
  background-color: transparent;
  border: none; }
  .about__show-all-text:focus {
    outline: none; }

@media (min-width: 576px) {
  margin: 0; }

@media (min-width: 992px) {
  margin: 0; }

@media (min-width: 1200px) {
  margin: 0; }

.page-footer {
  margin: 28px 0 0;
  padding: 60px 0 28px;
  text-align: center;
  color: #ffffff;
  background-color: #005bc1; }
  .page-footer a {
    color: inherit; }
    .page-footer a:hover, .page-footer a.active {
      color: #ffb90d; }

.page-footer__list {
  margin: 26px 37px 28px 12px;
  list-style: none; }
  .page-footer__list li {
    margin-right: 19px; }
  .page-footer__list a {
    font-size: 12px;
    line-height: 14px; }

.page-footer__contacts {
  display: block;
  margin-bottom: 40px;
  font-size: 24px;
  font-weight: 500;
  line-height: 28px; }

.page-footer__copyright {
  font-size: 12px;
  line-height: 14px; }

@media (min-width: 992px) {
  .page-footer {
    margin: 129px 0 0; } }

@media (min-width: 1200px) {
  .page-footer {
    margin: 173px 0 0; } }
