.nav-link {
  padding: 8px 7px 4px;

  font-size: $baseFontSize;
  line-height: $baseLineHeght;
  color: $color-base;

  &:hover {
    color: $hover-color;
  }
}

@media (min-width: $media-breakpoint-md) {
  .nav-item {
    position: relative;

    &.active,
    &:hover {
      .nav-link {
        &::before {
          content: "";

          position: absolute;
          top: -25px;
          left: 0;

          width: 100%;
          height: 10px;

          background: radial-gradient(50% 50% at 50% 50%, #0092f2 27.6%, #006be9 100%);
        }
      }
    }
  }
}
