.about {
  padding-top: 1px;
  padding-bottom: 1px;

  font-size: $largFontSize;
  line-height: $largLineHeght;
  color: $white;

  background: radial-gradient(50% 50% at 50% 50%, #0092f2 27.6%, #006be9 100%);

  img {
    position: relative;

    display: block;
  }

  p {
    margin-bottom: 21px;
  }
}

.about__wrapper {
  margin: 29px 17px;
}

.about__contacts {
  margin-top: 40px;
  max-width: 313px;
}

.about__text {
  margin: 0;
}

.about__show-all-text {
  display: block;
  margin: 0 auto;

  background-color: transparent;
  border: none;

  &:focus {
    outline: none;
  }
}

@media (min-width: $media-breakpoint-sm) {
  margin: 0;
}

@media (min-width: $media-breakpoint-lg) {
  margin: 0;
}

@media (min-width: $media-breakpoint-xl) {
  margin: 0;
}
